// export default AboutPage

export default function About() {
  return (
    <StaticQuery
      query={graphql`
        query {
          allContentfulThingsILove {
            edges {
              node {
                picture {
                  file {
                    url
                  }
                  fixed(width: 10, height: 10) {
                    base64
                    tracedSVG
                    aspectRatio
                    srcWebp
                    srcSetWebp
                  }
                }
                title
                body
              }
            }
          }
        }
      `}
      render={data => (
        <header>{data.allContentfulThingsILove.edges.node}</header>
      )}
    />
  )
} 
